<template>
  <div>

    <b-container class="bv-example-row">
      <b-row>
        <b-col align="start">
          <h2>Portfolio</h2>
          <select v-model="selectedPortfolioId"
                  @change="changePortfolio($event)">
            <option v-for="portfolio in portfolioList" 
                    :key="portfolio.portfolioId" 
                    v-bind:value="portfolio.portfolioId">
              {{ portfolio.name }}
            </option>
          </select>
        </b-col>
        <b-col align="end">
          
          <b-button id="show-btn" @click="$refs['add-portfolio'].show()">Add Portfolio</b-button>&nbsp;
          <b-button id="show-btn" @click="$refs['add-ticker'].show()">Add Ticker</b-button>
        </b-col>
      </b-row>

      <b-row>
        <portfolio-list ref="listRefreshRequest"
                        v-on:emitPortfolioList="setPortfolioList"
                        ></portfolio-list>
      </b-row>

      <!-- The modal -->
      <b-modal ref="add-portfolio" hide-footer title="Add Portfolio">
        <b-form-input v-model="portfolioName4Add" placeholder="Enter portfolio name"></b-form-input>
        <b-button class="mt-3" variant="outline-danger" block @click="addPortfolio">Save</b-button>
      </b-modal>
      

      <b-modal ref="add-ticker" hide-footer title="Add Ticker">
        <b-form-input v-model="ticker4Add" placeholder="Enter Ticker"></b-form-input>
        <b-button class="mt-3" variant="outline-warning" block @click="addTicker">Save</b-button>
      </b-modal>



    </b-container>

  </div>
</template>
<script>
  import PortfolioService from "@/services/portfolio.service.js";
  import PortfolioList from "./portfolio/PortfolioList.vue"

  export default {
    name: "portfolio",
    components: {
      /**
       * components
       */
      PortfolioList,
    },
    data () {
      return {
        portfolioList: [],        
        selectedPortfolioId: '',
        portfolioName4Add: '',
        ticker4Add: '',
      }
    },
    created() {
      
    },
    mounted() {
      /**
       * mounted
       */
    },
    computed: {
      /**
       * computed
       */
    },
    methods: {
      /**
       * methods
       */
      refresh() {
        this.listRefreshRequest()
      },
      listRefreshRequest(selectedPortfolioId) {
        this.$refs.listRefreshRequest.refresh(selectedPortfolioId)
      },
      setPortfolioList(obj) {
        console.log("setPortfolioList::obj="+obj)
        this.portfolioList = obj
        this.selectedPortfolioId = obj[0].portfolioId
      },
      changePortfolio(event) {
        console.log('changePortfolio='+event.target.value)
        this.selectedPortfolioId = event.target.value;
        console.log('this.selectedPortfolioId='+this.selectedPortfolioId)
        this.listRefreshRequest(this.selectedPortfolioId)
      },
      addPortfolio() {
        let param = { email: this.$store.state.email,
                      name: this.portfolioName4Add } 

        PortfolioService.addPortfolio(param)
        .then(res => {
          console.log('addPortfolio::res'+res);
          if (res.status === 200) {
            
            this.$refs.listRefreshRequest.getPortfolioList()
            this.portfolioName4Add = ''
            this.$refs['add-portfolio'].hide()
            alert('포트폴리오가 등록되었습니다.');
          } else {

          }
        })           
      },
      addTicker() {
        let param = { email: this.$store.state.email,
                      portfolioId: this.selectedPortfolioId,
                      ticker: this.ticker4Add } 

        PortfolioService.register(param)
        .then(res => {
          console.log('addTicker::res'+res);
          if (res.status === 200) {
            
            this.$refs.listRefreshRequest.refresh(this.selectedPortfolioId)
            this.ticker4Add = ''
            //this.$refs['add-ticker'].hide()
            //alert('Ticker가 등록되었습니다.');
          } else {

          }
        })

      }
    }
  }
</script>
