<template>
  <div>
    <b-table striped hover 
             :items="items" 
             :fields="fields"
             @row-dblclicked="(item, index, event) => rowDblClickHandler(item, index, event)"
             >
      <template #cell(actions)="row">
        <b-icon style="cursor: pointer;" 
                icon="trash-fill" 
                variant="dark"
                @click="del(row.item, row.index, $event.target)"></b-icon>
      </template>
    </b-table>
  </div>
</template>

<script>
  import PortfolioService from "@/services/portfolio.service.js";

  export default {
    name: "portfolio-list",
    components: {
      /**
       * components
       */
    },
    data () {
      return {
        // Note 'isActive' is left out and will not appear in the rendered table
        fields: [
          {
            key: 'businessCycle',
            label: '경기순환/섹터',
            sortable: true
          },
          {
            key: 'ticker',
            label: '티커',
            sortable: true
          },
          {
            key: 'currentPrice',
            label: '현재가',
            sortable: false,
            variant: 'danger'
          },
          {
            key: 'unitPrice',
            label: '매수단가',
            sortable: false
          },
          {
            key: 'stockNum',
            label: '주식수',
            sortable: false
          },
          {
            key: 'totalTradingAmount',
            label: '매수금액',
            sortable: false
          },
          {
            key: 'evalAmount',
            label: '평가금액',
            sortable: false
          },
          {
            key: 'earningAmount',
            label: '수익금액',
            sortable: false
          },
          {
            key: 'earningRate',
            label: '수익률',
            sortable: false
          },
          {
            key: 'annualPayout',
            label: '주당배당금',
            sortable: false
          },
          {
            key: 'totalPayout',
            label: '총배당금',
            sortable: false
          },
          {
            key: 'investmentDivYield',
            label: '투자배당률',
            sortable: false
          },
          {
            key: 'dividendPayMonth',
            label: '배당주기',
            sortable: false
          },
          {
            key: 'portion',
            label: '비중',
            sortable: false
          },
          {
            key: 'actions',
            label: 'Actions',
          },
        ],
        items: [
          { isActive: true, age: 40, first_name: 'Dickerson', last_name: 'Macdonald' },
          { isActive: false, age: 21, first_name: 'Larsen', last_name: 'Shaw' },
          { isActive: false, age: 89, first_name: 'Geneva', last_name: 'Wilson' },
          { isActive: true, age: 38, first_name: 'Jami', last_name: 'Carney' }
        ],
        portfolioList: [],
        selectedPortfolioId: '',
      }
    },
    created() {

    },
    mounted() {  
      this.getPortfolioList()
    },
    methods: {
      refresh(portfolioId) {
        //this.getPortfolioList()
        console.log('this.selectedPortfolioId='+this.selectedPortfolioId+', portfolioId='+portfolioId)
        if ( portfolioId ) this.selectedPortfolioId = portfolioId
        this.getData(this.selectedPortfolioId)
      },
      getPortfolioList() {
        let param = { email: this.$store.state.email } 
        console.log('param='+JSON.stringify(param))

        PortfolioService.getPortfolioList(param)
        .then(res => {
          console.log(res);
          if (res.status === 200) {
            this.portfolioList = res.data
            this.selectedPortfolioId = res.data[0].portfolioId
            this.getData(this.selectedPortfolioId)

            this.$emit('emitPortfolioList', this.portfolioList)
          } else {

          }
        })    
      },
      getData(portfolioId) {
        console.log(">>>>> getData::portfolioList::getData::protfolioId="+portfolioId+", selectedPortfolioId="+this.selectedPortfolioId)
        let param = { email: this.$store.state.email
                    , portfolioId: portfolioId
                    } 
        PortfolioService.getPortfolioStockList(param)
        .then(res => {
          console.log(res);
          if (res.status === 200) {
            //this.items = res.data.list
            console.log('res.data='+res.data)
            let tmp_items = []
            res.data.forEach((element, index) => {
              console.log('index='+index+', element='+element)
              var obj = {
                businessCycle: element.stock.businessCycle + '/' + element.stock.sector,
                ticker: element.stock.ticker,
                currentPrice: element.stock.currentPrice,
                unitPrice: element.unitPrice,
                stockNum: element.stockNum,
                totalTradingAmount: element.unitPrice * element.stockNum,
                evalAmount: element.stock.currentPrice * element.stockNum,
                earningAmount : (element.stock.currentPrice * element.stockNum) - (element.unitPrice * element.stockNum),
                earningRate: ((element.stock.currentPrice * element.stockNum) - (element.unitPrice * element.stockNum)) / (element.unitPrice * element.stockNum),
                annualPayout: element.stock.annualPayout,
                totalPayout : element.stock.annualPayout * element.stockNum,
                investmentDivYield: (element.stock.annualPayout * element.stockNum) / (element.unitPrice * element.stockNum),
                dividendPayMonth: element.stock.dividendPayMonth,
                portion: 0
              }
              console.log('obj='+obj)
              tmp_items.push(obj)
            })
            this.items = tmp_items
          } else {
          }
        })    
      }, 
      rowDblClickHandler(obj, index, event) {
        console.log('rowDblClickHandler::obj='+JSON.stringify(obj))
        //this.fields.push({ key: 'actions', label: 'Actions' })
        
      },
      del(item, index, event) {
        
        console.log('del::index='+index+', item='+JSON.stringify(item))
        console.log('>>>>>>>>>>>>>>>>>>')
        console.log('this.selectedPortfolioId='+this.selectedPortfolioId)

        let param = { email: this.$store.state.email
                    , portfolioId: this.selectedPortfolioId
                    , ticker: item.ticker
                    } 

        if(confirm("삭제하시겠습니까?")) {
          PortfolioService.remove(param)
          .then(
            res => {
              if (res.status === 200) {
                this.refresh(this.selectedPortfolioId)
                alert("삭제되었습니다.")
              } else {
                alert("실행중 실패했습니다.\n다시 이용해 주세요.")
              }
            })
        }

      }
      
    }
  }
</script>
<style lang="scss">

</style>    