<template>
  <div>
    <b-table striped hover 
             :items="items" 
             :fields="fields">

      <template #cell(actions)="row">
        <b-icon style="cursor: pointer;" 
                icon="trash-fill" 
                variant="dark"
                @click="del(row.item, row.index, $event.target)"></b-icon>
      </template>      

    </b-table>
  </div>
</template>

<script>
  import InterestStockService from "@/services/interestStock.service.js";

  export default {
    name: "interestStock-list",
    components: {
      /**
       * components
       */
    },
    data () {
      return {
        // Note 'isActive' is left out and will not appear in the rendered table
        fields: [
          {
            key: 'businessCycle',
            label: '경기순환/섹터',
            sortable: true
          },
          {
            key: 'ticker',
            label: '티커',
            sortable: true
          },
          {
            key: 'currentPrice',
            label: '현재가',
            sortable: false,
            variant: 'danger'
          },
          {
            key: 'desiredPurchasePrice',
            label: '매수희망가',
            sortable: false
          },
          {
            key: 'avgPrice',
            label: '평균가(1년)',
            sortable: false
          },
          {
            key: 'under10Price',
            label: '10% 하락',
            sortable: false
          },
          {
            key: 'under20Price',
            label: '20% 하락',
            sortable: false
          },
          {
            key: 'under30Price',
            label: '30% 하락',
            sortable: false
          },         
          {
            key: 'w52',
            label: '최저-52주-최고',
            sortable: false
          },
          {
            key: 'divYield',
            label: '배당수익률',
            sortable: false
          },
          {
            key: 'fiveYearAvgDividendYield',
            label: '5년평균배당률',
            sortable: false
          },
          {
            key: 'payoutRatio',
            label: '배당성향',
            sortable: false
          },
          {
            key: 'dividendGrowth',
            label: '배당성장년수',
            sortable: false
          },
          {
            key: 'annualPayout',
            label: '배당금',
            sortable: false
          },
          {
            key: 'dividendPayMonth',
            label: '배당주기',
            sortable: false
          },
          {
            key: 'actions',
            label: 'Actions',
          },          
        ],
        items: [
          { isActive: true, age: 40, first_name: 'Dickerson', last_name: 'Macdonald' },
          { isActive: false, age: 21, first_name: 'Larsen', last_name: 'Shaw' },
          { isActive: false, age: 89, first_name: 'Geneva', last_name: 'Wilson' },
          { isActive: true, age: 38, first_name: 'Jami', last_name: 'Carney' }
        ],
      }
    },
    created() {

    },
    mounted() {  
      this.getData()
    },
    methods: {
      refresh() {
        //this.getPortfolioList()
        this.getData()
      },
      getData() {
        let param = { email: this.$store.state.email
                    } 
        InterestStockService.getList(param)
        .then(res => {

          console.log(res);
          if (res.status === 200) {
            //this.items = res.data.list
            console.log('res.data='+res.data)
            let tmp_items = []
            res.data.forEach((element, index) => {
              console.log('index='+index+', element='+element)
              
              var obj = {
                interestStockId: element.interestStockId,
                businessCycle: element.stock.businessCycle + '/' + element.stock.sector,
                ticker: element.stock.ticker,
                currentPrice: Number(element.stock.currentPrice).toFixed(2),
                desiredPurchasePrice: 0,  //매수희망가
                avgPrice: 0,  // 1년 평균가
                under10Price: Number(element.stock.highestPrice * 0.9).toFixed(2), // 최고점 대비 10% 하락
                under20Price: Number(element.stock.highestPrice * 0.8).toFixed(2), // 최고점 대비 20% 하락
                under30Price: Number(element.stock.highestPrice * 0.7).toFixed(2), // 최고점 대비 30% 하락
                w52: 0,
                divYield: element.stock.dividendYield,
                fiveYearAvgDividendYield: element.stock.fiveyearAvgDividendYield,
                payoutRatio: element.stock.payoutRatio,
                dividendGrowth: element.stock.dividendGrowthRate,
                annualPayout: element.stock.annualPayout,
                dividendPayMonth: element.stock.dividendPayMonth,
              }
              console.log('obj='+obj)
              tmp_items.push(obj)
              
            })
            this.items = tmp_items
          } else {
          }
        })    
      },      
      del(item, index, event) {
        
        console.log('del::index='+index+', item='+JSON.stringify(item))
        console.log('>>>>>>>>>>>>>>>>>>')

        if(confirm("삭제하시겠습니까?")) {
          InterestStockService.remove(item.interestStockId)
          .then(
            res => {
              if (res.status === 200) {
                this.refresh()
                alert("삭제되었습니다.")
              } else {
                alert("실행중 실패했습니다.\n다시 이용해 주세요.")
              }
            })
        }

      }      
    }
  }
</script>
<style lang="scss">

</style>    