<template>
  <div>

    <b-container class="bv-example-row">
      <b-row>
        <b-col align="start">
          <h2>매매일지</h2>
        </b-col>
        <b-col align="end">
        </b-col>
      </b-row>

      <b-row>
        <b-table-simple hover small caption-top responsive>
          <caption>Register or Modify Trading note</caption>
          <b-thead head-variant="dark">
            <b-tr>
              <b-th>포트폴리오</b-th>
              <b-th>거래일</b-th>
              <b-th>티커</b-th>
              <b-th>체결단가</b-th>
              <b-th>수량</b-th>
              <b-th>매매구분</b-th>
              <b-th>거래금액</b-th>
              <b-th>수수료</b-th>
              <b-th>환율</b-th>
              <b-th>action</b-th>
            </b-tr>
          </b-thead>
          <b-tbody>
            <b-tr>
              <b-td>
                <b-form-select class="mt-3 lg" v-model="selectedPortfolioId" :options="options"></b-form-select>
              </b-td>
              <b-td>
                <b-form-input class="mt-3" v-model="tradingDate" placeholder="YYYY/MM/DD"></b-form-input>
              </b-td>
              <b-td>
                <b-form-input class="mt-3" v-model="ticker" placeholder="AAPL"></b-form-input>
              </b-td>
              <b-td>
                <b-form-input class="mt-3" v-model="unitPrice" placeholder=""></b-form-input>
              </b-td>
              <b-td>
                <b-form-input class="mt-3" v-model="stockNum" placeholder=""></b-form-input>
              </b-td>
              <b-td>
                <b-form-select class="mt-3 lg" v-model="tradingType" :options="options2"></b-form-select>
              </b-td>
              <b-td>
                <b-form-input class="mt-3" v-model="tradingAmount" placeholder=""></b-form-input>
              </b-td>
              <b-td>
                <b-form-input class="mt-3" v-model="fee" placeholder=""></b-form-input>
              </b-td>
              <b-td>
                <b-form-input class="mt-3" v-model="exchangeRate" placeholder=""></b-form-input>
              </b-td>
              <b-td>
                <b-button v-if="input_mode" class="mt-3" variant="outline-warning" block @click="add">추가</b-button>
                <template v-else>
                  <b-button class="mt-3" variant="outline-warning" block @click="mod">수정</b-button>
                  <b-button class="mt-3" variant="outline-warning" block @click="clear">초기화</b-button>
                </template>
              </b-td>
            </b-tr>
          </b-tbody>
        </b-table-simple>
        
        
      </b-row>
      <b-row>
        <b-table striped hover 
                 :items="items" 
                 :fields="fields"
                 @row-dblclicked="(item, index, event) => rowDblClickHandler(item, index, event)">

          <template #cell(actions)="row">
            <b-icon style="cursor: pointer;" 
                    icon="trash-fill" 
                    variant="dark"
                    @click="del(row.item, row.index, $event.target)"></b-icon>
          </template>                 
        </b-table>
      </b-row>

      <!-- The modal -->
      <!--       
      <b-modal ref="add-ticker" hide-footer title="Add Ticker">
        <b-form-input v-model="ticker4Add" placeholder="Enter Ticker"></b-form-input>
        <b-button class="mt-3" variant="outline-warning" block @click="addTicker">Save</b-button>
      </b-modal> -->



    </b-container>

  </div>
</template>
<script>
  import TradingNoteService from "@/services/tradingNote.service.js";
  
  export default {
    name: "tradingNote",
    components: {
      /**
       * components
       */
    },
    data () {
      return {
        fields: [
          { key: 'portfolioName', 
            label: '포트폴리오'
          },          
          { key: 'tradingDate', 
            label: '거래일'
          },
          { key: 'ticker', 
            label: '티커'
          },
          { key: 'unitPrice', 
            label: '체결단가'
          },
          { key: 'stockNum', 
            label: '수량'
          },
          { key: 'tradingType', 
            label: '매매구분'
          },
          { key: 'tradingAmount', 
            label: '거래금액'
          },
          { key: 'fee', 
            label: '수수료'
          },
          { key: 'exchangeRate', 
            label: '환율'
          },
          {
            key: 'actions',
            label: 'Actions',
          },          
        ],
        items: [
          { isActive: true, age: 40, first_name: 'Dickerson', last_name: 'Macdonald' },
          { isActive: false, age: 21, first_name: 'Larsen', last_name: 'Shaw' },
          { isActive: false, age: 89, first_name: 'Geneva', last_name: 'Wilson' },
          { isActive: true, age: 38, first_name: 'Jami', last_name: 'Carney' }
        ], 
        options: [], 
        options2: [ { value: '매수', text: '매수'},
                    { value: '매도', text: '매도'} ], 
        tradingNoteId: '',
        tradingDate: '',
        ticker: '',
        unitPrice: '',
        stockNum: '',
        tradingType: '매수',
        tradingAmount: '',
        fee: '',
        exchangeRate: '',
        portfolioList: [],
        selectedPortfolioId: '',      
        input_mode: true
      }
    },
    created() {
      
    },
    mounted() {
      /**
       * mounted
       */
      this.getData()
      this.getPortfolioList()
    },
    computed: {
      /**
       * computed
       */
    },
    methods: {
      /**
       * methods
       */
      refresh() {
        this.getData()
      },
      getPortfolioList() {
        let param = { email: this.$store.state.email } 
        console.log('param='+JSON.stringify(param))

        TradingNoteService.getPortfolioList(param)
        .then(res => {
          console.log(res);
          if (res.status === 200) {
            this.portfolioList = res.data
            this.selectedPortfolioId = res.data[0].portfolioId
            let tmp_items = []
            res.data.forEach((element, index) => {
              var obj = {
                value: element.portfolioId,
                text: element.name
              }
              tmp_items.push(obj)
            })
            this.options = tmp_items
          } else {

          }
        })    
      },      
      getData() {
        let param = { email: this.$store.state.email
                    } 
        TradingNoteService.getList(param)
        .then(res => {

          console.log('getData.res='+JSON.stringify(res));
          if (res.status === 200) {
            //this.items = res.data.list
            let tmp_items = []
            res.data.forEach((element, index) => {
              console.log('index='+index+', element='+element)
              console.log('element.portfolioStock='+element.portfolioStock)

              var obj = {
                tradingNoteId: element.tradingNoteId,
                portfolioId: element.portfolioStock.portfolio.portfolioId,
                portfolioName: element.portfolioStock.portfolio.name,
                tradingDate: element.tradingDate,
                ticker: element.portfolioStock.stock.ticker,
                unitPrice: element.unitPrice,
                stockNum: element.stockNum,
                tradingType: element.tradingType,
                tradingAmount: element.tradingAmount,
                fee: element.fee,
                exchangeRate: element.exchangeRate,
              }
              tmp_items.push(obj)
              
            })
            this.items = tmp_items
          } else {
          }
        })    
      },  
      add() {
        let param = { email: this.$store.state.email,
                      portfolioId: this.selectedPortfolioId,
                      ticker: this.ticker,
                      tradingDate: this.tradingDate,
                      tradingType: this.tradingType,
                      stockNum: this.stockNum,
                      unitPrice: this.unitPrice,
                      tradingAmount: this.tradingAmount,
                      fee: this.fee,
                      exchangeRate: this.exchangeRate
                    } 
        console.log('param='+JSON.stringify(param))
        TradingNoteService.register(param)
        .then(res => {
          console.log('add::res'+res);
          if (res.status === 200) {
            
            //this.$refs.listRefreshRequest.refresh(this.selectedPortfolioId)
            //this.ticker4Add = ''
            this.refresh()
            alert('매매일지가 등록 되었습니다.');
          } else {

          }
        })

      },
      mod() {
        let param = { email: this.$store.state.email,
                      portfolioId: this.selectedPortfolioId,
                      tradingNoteId: this.tradingNoteId,
                      ticker: this.ticker,
                      tradingDate: this.tradingDate,
                      tradingType: this.tradingType,
                      stockNum: this.stockNum,
                      unitPrice: this.unitPrice,
                      tradingAmount: this.tradingAmount,
                      fee: this.fee,
                      exchangeRate: this.exchangeRate
                    } 
        console.log('param='+JSON.stringify(param))
        TradingNoteService.modify(param)
        .then(res => {
          console.log('add::res'+res);
          if (res.status === 200) {
            
            //this.$refs.listRefreshRequest.refresh(this.selectedPortfolioId)
            //this.ticker4Add = ''
            this.clear()
            this.refresh()
            alert('매매일지가 수정 되었습니다.');
          } else {

          }
        })
      },
      del(item, index, event) {

        console.log('del::index='+index+', item='+JSON.stringify(item))
        console.log('>>>>>>>>>>>>>>>>>>')

        if(confirm("삭제하시겠습니까?")) {
          TradingNoteService.remove(item.tradingNoteId)
          .then(
            res => {
              if (res.status === 200) {
                this.clear()
                this.refresh(this.selectedPortfolioId)
                alert("삭제되었습니다.")
              } else {
                alert("실행중 실패했습니다.\n다시 이용해 주세요.")
              }
            })
        }
      },
      clear() {
        this.input_mode = true
        this.tradingNoteId = ''
        this.tradingDate = ''
        this.ticker = ''
        this.unitPrice = ''
        this.stockNum = ''
        this.tradingAmount = ''
        this.fee = ''
        this.exchangeRate = ''
      },
      rowDblClickHandler(obj, index, event) {
        console.log('rowDblClickHandler...')
        console.log('index='+index+', obj.ticker='+obj.ticker+', event='+event)
        this.input_mode = false
        this.tradingNoteId = obj.tradingNoteId
        this.selectedPortfolioId = obj.portfolioId
        this.tradingDate = obj.tradingDate
        this.ticker = obj.ticker
        this.unitPrice = obj.unitPrice
        this.stockNum = obj.stockNum
        this.tradingType = obj.tradingType
        this.tradingAmount = obj.tradingAmount
        this.fee = obj.fee
        this.exchangeRate = obj.exchangeRate
      }

    }
  }
</script>
<style lang="scss">

</style>